import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';
import {ConfirmDialogComponent} from '../confirm-dialog/confirm-dialog.component';
import {OperType} from "../OperType";
import {DialogMet} from "../DialogMet";
import {ModalActions} from "../ModalActions";

@Component({
  selector: 'app-edit-priority-dialog',
  templateUrl: './edit-priority-dialog.component.html',
  styleUrls: ['./edit-priority-dialog.component.css']
})

// / 
export class EditPriorityDialogComponent extends DialogMet<EditPriorityDialogComponent> implements OnInit {

  public dialogTitle: string; //    
  public priorityTitle: string; //     (   )
  public operType: OperType;

  constructor(
      public dialogRef: MatDialogRef<EditPriorityDialogComponent>, // //      . 
      @Inject(MAT_DIALOG_DATA) private data: [string, string, OperType], // ,     
      public dialog: MatDialog //      ( ) -    
  ) {
    super(dialogRef);
  }

  ngOnInit() {
    this.priorityTitle = this.data[0];
    this.dialogTitle = this.data[1];
    this.operType = this.data[2];
  }

  public onConfirm(): void {
    this.dialogRef.close(this.priorityTitle);
  }

  //  
  public delete(): void {
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      maxWidth: '500px',
      data: {
        dialogTitle: ' ',
        message: `    : "${this.priorityTitle}"? (   '')`
      },
      autoFocus: false
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.dialogRef.close(ModalActions.delete); //  
      }
    });
  }
  public canDelete(): boolean {
    return this.operType === OperType.EDIT;
  }
}
